@font-face {
  font-family: "GothamB", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  src: url("./fonts/Gotham-Bold.otf") format("truetype");
}
@font-face {
  font-family: "GothamL", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  src: url("./fonts/Gotham-XLight.otf") format("truetype");
}

@font-face {
  font-family: "RixL", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  src: url("./fonts/RixL.ttf") format("truetype");
}
@font-face {
  font-family: "RixM", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  src: url("./fonts/RixM.ttf") format("truetype");
}
@font-face {
  font-family: "RixB", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  src: url("./fonts/RixB.ttf") format("truetype");
}
body {
  font-family: "GothamB", sans-serif;

  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
-webkit-font-smoothing: antialiased;
-moz-osx-font-smoothing: grayscale; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a:link {
}
a:visited {
}
a:hover {
}
a:active {
}

.show {
  display: block;
}
.hide {
  display: none;
}

@media (min-width: 600px) {
  .selectbox {
    width: 50%;
    display: inline-block;
  }
  .image_large {
    display: block;
  }
  .image_small {
    display: none;
  }
}
@media (max-width: 600px) {
  .selectbox {
    width: 100%;
    height: 10vh;
  }
  .agree {
    margin-left: 17vw;
  }
  .image_large {
    display: none;
  }
  .image_small {
    display: block;
  }
}

.btn {
  background-color: #fff;
  border: 1px solid #1f3a93;
  color: #203a8f;
}
.btn.active {
  background-color: #203a8f;
  color: #fff;
  border: 1px solid #203a8f;
}

.rs-picker-select-menu-item:hover {
  background-color: #c7d0ee !important;
  color: #555555 !important;
}
.rs-picker-select-menu-items {
  text-align: center;
}
/* 페이지네이션 */
.goFirst {
  font-size: 0.9vw;
  width: 2vw;
}
.prev,
.next {
  font-size: 0.9vw !important;
  width: 3.5vw !important;
  margin-right: 0.3vw;
}
.goLast {
  font-size: 0.9vw;
  width: 2vw;
}
@media screen and (max-width: 1500px) {
  .goFirst,
  .goLast {
    font-size: 2vw !important;
  }
  .prev,
  .next {
    width: 8vw !important;
    font-size: 2vw !important;
  }
}
@media (min-width: 768px) and (max-width: 1500px) and (orientation: landscape) {
  .goFirst,
  .goLast {
    font-size: 1.2vw !important;
  }
  .prev,
  .next {
    width: 5vw !important;
    font-size: 1.2vw !important;
  }
}

.hollyn div:nth-child(2) div button {
  padding: 7px 19px;
  border-radius: 2px;
  background-color: #fff;
  font-size: 14px;
  color: #203a8f;
  border: 1px solid #203a8f;
}
.hollyn div:nth-child(2) div button:hover {
  color: #fff;
  background-color: #203a8f !important;
}

.hollyn div:nth-child(2) div:nth-child(2) button {
  padding: 7px 19px;
  border-radius: 2px;
  background-color: #fff;
  font-size: 14px;
  color: #ff4949;
  border: 1px solid #ff4949;
}
.hollyn div:nth-child(2) div:nth-child(2) button:hover {
  color: #fff;
  background-color: #ff4949 !important;
}

.hollyn div:nth-child(3) div button {
  padding: 7px 19px;
  border-radius: 2px;
  background-color: #fff;
  font-size: 14px;
  color: #203a8f;
  border: 1px solid #203a8f;
}
.hollyn div:nth-child(3) div button:hover {
  color: #fff;
  background-color: #203a8f !important;
}

.swal-text {
  color: #555555;
  font-weight: 500;
}
.swal-icon--success__ring {
  color: red;
}
.swal-button:focus {
  display: none;
}
input:focus {
  border: 2px solid #203a8f;
  outline: none;
}

