table {
  width: 100%;
  height: 100%;
  text-align: left;
}
table thead tr th:first-child {
  border-radius: 5px 0 0 0;
}
table thead tr th:last-child {
  border-radius: 0 5px 0 0;
}
table thead tr th {
  height: 50px;
  background-color: #203a8f;
  color: white;
  border-bottom: none;
  font-size: 16px;
  font-weight: 500;
}
table tbody {
  background: rgba(217, 217, 217, 0.1);
}
table tbody tr:hover {
  background: #c8cfe4;
}
table tbody tr {
  height: 60px;
}
table tbody tr td {
  border: none;
}
table thead tr th {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 400px;
  -webkit-box-orient: vertical;
}
table tbody tr td {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 400px;
  -webkit-box-orient: vertical;
}
table tbody td,
table tbody th {
  border-top: 1px solid #ccc;
  word-break: break-all;
}
table--min {
  min-width: 700px;
}

@media screen and (max-width: 1600px) {
  /*normal*/
  table-box {
    overflow-x: auto;
  }
}
