.loginBtn {
  position: relative;
  min-width: 160px;
  min-height: 50px;
  border-radius: 50px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid white;
}
.loginBtn:focus {
  display: none;
}
.loginBtn .text,
.loginBtn .icon-container {
  position: relative;
  padding-left: 20px;
  z-index: 2;
}
.loginBtn .icon-container {
  position: relative;
  width: 25px;
  height: 25px;
  transition: transform 500ms ease;
}
