  position: absolute;
  left: 0;
  top: 0;
  width: var(--icon-size);
  height: var(--icon-size);
  transition: transform 500ms ease, opacity 250ms ease;
} */
.loginBtn .icon-container {
  transform: translateX(-200%);
  opacity: 0;
}
.loginBtn::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #203a8f;
  border-radius: 50px;
  z-index: 1;
  transition: transform 500ms ease;
}
.loginBtn:hover::after {
  transform: translateX(68%);
}
.loginBtn:hover .icon-container {
  transform: translateX(125%);
}
.loginBtn:hover .text {
  color: #203a8f;
}
